						<el-option label="碩士畢業" value="碩士畢業"></el-option>
						<el-option label="博士畢業" value="博士畢業"></el-option>
						<el-option label="海外大學畢業" value="海外大學畢業"></el-option>
						<el-option label="海外碩士畢業" value="海外碩士畢業"></el-option>
						<el-option label="海外博士畢業" value="海外博士畢業"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="地區">
					<el-select v-model="filter.Location" placeholder="請選擇">
						<el-option label="全部" value=""></el-option>
						<el-option label="北部" value="北部"></el-option>
