			<div class="text-right">
				<el-pagination :current-page.sync="currentPage" :page-sizes="[50, 100, 200, 300]" :page-size.sync="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="filteredSalary.length"></el-pagination>
			</div>
			<el-table size="small" :data="filteredSalary.slice(pageStart, pageStart + pageSize)" v-loading="loading" element-loading-text="資料太豐富，努力下載中" v-loading.fullscreen="true" stripe max-height="640">
				<el-table-column type="index" width="50" align="center"></el-table-column>
				<el-table-column prop="CompanyName" label="公司名稱"></el-table-column>
				<el-table-column prop="Category" label="產業類別" align="center"></el-table-column>
				<el-table-column prop="JobTitle" label="職稱"></el-table-column>
