				<el-table-column prop="PublishDate" label="發佈時間" align="center"></el-table-column>
				<el-table-column label="內容" align="center">
					<template slot-scope="scope">
						<el-link type="primary" :underline="false" @click="showDetail(scope.row)">點我看內容</el-link>
					</template>
				</el-table-column>
			</el-table>
			<div class="text-right">
				<el-pagination :current-page.sync="currentPage" :page-sizes="[50, 100, 200, 300]" :page-size.sync="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="filteredSalary.length"></el-pagination>
			</div>
			<el-dialog :title="detail.CompanyName" :visible.sync="dialogVisible" width="40%">
				<el-form ref="form">
					<el-form-item label="職稱：">
						<span>{{detail.JobTitle}}</span>
					</el-form-item>
					<el-form-item label="產業類別：">
						<span>{{detail.Category}}</span>
					</el-form-item>
					<el-form-item label="上班地點：">
						<span>{{detail.JobTitle}}</span>
					</el-form-item>
					<el-form-item label="每月薪資：">
						<span>{{detail.Salary}}</span>
					</el-form-item>
					<el-form-item label="相關工作經驗：">
						<span>{{detail.Experience}}</span>
					</el-form-item>
					<el-form-item label="最高學歷：">
						<span>{{detail.School}}</span>
					</el-form-item>
					<el-form-item label="學校科系：">
						<span>{{detail.Department}}</span>
					</el-form-item>
					<el-form-item label="面談日期：">
						<span>{{detail.OfferDate}}</span>
					</el-form-item>
					<el-form-item label="其他訊息：">
						<div v-html="detail.Others" class="other"></div>
