</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import * as firebase from 'firebase';

const firebaseConfig = {
	apiKey: 'AIzaSyAZOAPVY5L9dc7wS3UkRdL2wsvymRTfuCA',
	authDomain: 'salary-spider.firebaseapp.com',
	databaseURL: 'https://salary-spider.firebaseio.com',
	projectId: 'salary-spider',
	storageBucket: 'salary-spider.appspot.com',
	messagingSenderId: '617990298251',
	appId: '1:617990298251:web:14c92f6bfd5baf6ce5528e'
};
const app = firebase.initializeApp(firebaseConfig);

export default {
	name: 'home',
	data() {
		return {
			filter: {
				CompanyName: '',
				Category: '',
				School: '',
				Location: ''
			},
			loading: false,
			dialogVisible: false,
			currentPage: 1,
			pageSize: 50,
			salary: Object.freeze([]),
			detail: {}
		};
	},
	mounted() {
		this.getSalary();
	},
	computed: {
		pageStart() {
			return (this.currentPage - 1) * this.pageSize;
		},
		filteredSalary() {
			const CompanyName = this.filter.CompanyName.toUpperCase();
			const Category = this.filter.Category.toUpperCase();
			const School = this.filter.School.toUpperCase();
			const Location = this.filter.Location.toUpperCase();

			if (!CompanyName && !Category && !School && !Location) {
				return this.salary;
			}

			return this.salary.filter(item => {
				return (
					(CompanyName === '' ||
						item.CompanyName.toUpperCase().indexOf(CompanyName) >
							-1) &&
					(Category === '' ||
						item.Category.toUpperCase().indexOf(Category) > -1) &&
					(School === '' ||
						item.School.toUpperCase().indexOf(School) > -1) &&
					(Location === '' ||
						item.Location.toUpperCase().indexOf(Location) > -1)
				);
			});
		}
	},
	methods: {
		getSalary() {
			this.loading = true;
			const result = app
				.database()
				.ref('salary')
				.orderByChild('Page');
			result.once('value').then(snapshot => {
				let data = [];
				snapshot.forEach(item => {
					data.push(item.val());
				});
				this.salary = Object.freeze(data);
				this.loading = false;
			});
		},
		showDetail(row) {
			this.detail = row;
			this.detail.Others = this.detail.Others.replace(/\n/g, '<br>');
			this.dialogVisible = true;
		}
	}
};
</script>
